<template>
  <div class="leaflet-map">
  </div>
</template>

<script>
import 'leaflet-map'
import * as L from 'leaflet'

export default {
  props:['url'],
  data: function () {
    return {
      url:'',
      json: {},
    }
  },
  mounted () {
    this.getjson()
  },
  methods: {
    getjson: function () {
      this.$http.get(this.url).then(
        function (res) {
          // 处理成功的结果
          this.json = res.data
          console.log(this.json)
          this.drawmap()
        }, function (res) {
        console.log('fail json')
      }
      )
    },
    drawmap: function () {
      L.Icon.Default.imagePath = 'https://unpkg.com/leaflet@1.0.3/dist/images'
      var map = L.map(this.$el).setView([this.json.ViewX, this.json.ViewY], this.json.ViewScale)
      L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png', {
        attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
      }).addTo(map)

      L.marker([this.json.MyposX, this.json.MyposY]).addTo(map)
        .bindPopup(this.json.MyInfo)
        .openPopup()
      for (var index = 0; index > -1; index++) {
        if (this.json[index] == null) {
          break
        }
        var element = this.json[index]
        if (element.posX != null) {
          L.circle([element.posX, element.posY], element.Rad, { opacity: 0, fillColor: element.FillColor, fillOpacity: element.FillOpcity }).bindPopup(element.Info).on('mouseover', function (e) { this.openPopup() }).on('mouseout', function (e) { this.closePopup() }).addTo(map)
        }
      }
    }

  }
}
</script>

<style lang="scss">
@import "../../../../node_modules/leaflet/dist/leaflet.css";
@import "../../../sass/_variables.scss";

.leaflet-map {
  height: 100%;
}
</style>



